import React, { useState } from 'react';
import { filter } from 'lodash';
import PropTypes from 'prop-types';

import {
  FormControlLabel,
  Switch,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from '@mui/material';
import format from 'date-fns/format';

import Scrollbar from '../Scrollbar';
import NotificationListToolbar from './NotificationListToolbar';

import SearchNotFound from '../SearchNotFound';
import NotificationMoreMenu from './NotificationMoreMenu';

const TABLE_HEAD = [
  { id: 'name', label: 'Name', alignRight: false },
  { id: 'notificationType', label: 'Notification Type', alignRight: false },
  { id: 'type', label: 'Type', alignRight: false },
  { id: 'users', label: 'Total Users', alignRight: false },
  { id: 'dateTime', label: 'Date Time', alignRight: false },
  { id: 'active', label: 'Active', alignRight: false },
  { id: 'action', label: '', alignRight: false },
];

function applyFilter(array, query) {
  const stabilizedThis = array.map((el, index) => [el, index]);

  if (query) {
    return filter(array, (_notification) => _notification.name.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis.map((el) => el[0]);
}

NotificationListTable.propTypes = {
  notificationList: PropTypes.array,
};

function NotificationListTable({ notificationList = [] }) {
  const [filterName, setFilterName] = useState('');

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const filteredNotifications = applyFilter(notificationList, filterName);

  const isNotificationNotFound = filteredNotifications.length === 0;

  return (
    <Scrollbar>
      <NotificationListToolbar filterName={filterName} onFilterName={handleFilterByName} />

      <TableContainer sx={{ minWidth: 800 }}>
        <Table aria-label="notification table">
          <TableHead>
            <TableRow>
              {TABLE_HEAD.map((headCell) => (
                <TableCell key={headCell.id} align={headCell.alignRight ? 'right' : 'left'}>
                  {headCell.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {filteredNotifications.map((row) => (
              <TableRow key={row.id}>
                <TableCell>{row.name}</TableCell>
                <TableCell>{row.notificationType}</TableCell>
                <TableCell>{row.type}</TableCell>
                <TableCell>{row?.users?.length}</TableCell>
                <TableCell>{format(row.dateTime, 'MM/dd/yyyy HH:MM')}</TableCell>
                <TableCell>
                  <FormControlLabel
                    labelPlacement="start"
                    control={<Switch />}
                    label={<Typography variant="subtitle2">Active</Typography>}
                    sx={{ mx: 0, width: 1, justifyContent: 'center' }}
                  />
                </TableCell>
                <TableCell>
                  <NotificationMoreMenu />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
          {isNotificationNotFound && (
            <TableBody>
              <TableRow>
                <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                  <SearchNotFound searchQuery={filterName} />
                </TableCell>
              </TableRow>
            </TableBody>
          )}
        </Table>
      </TableContainer>
    </Scrollbar>
  );
}

export default NotificationListTable;
